$c1: #03065D
$c2: #000000

$cb11: #DDFEF9
$cb12: #C7FCF2
$cb13: #00E8B6

$cb21: #F4EFFC
$cb22: #F0E7FF
$cb23: #CEB0FF

$cb31: #FFEAEF
$cb32: #FFDFE7
$cb33: #FFB4F6

.d-none
	display: none !important

.cta_carousel_block
	border-radius: 30px

	&.cta_carousel_lime
		background-color: $cb11
		background-image: url(../img/cta_bg_10.png)
		background-position: bottom right
		background-size: cover
	.cta_carousel_block_wrapper
		padding: 40px 60px
		display: flex
		flex-direction: row
		.cta_carousel_block_text
			.h3
				color: $c1
				font-size: 40px
				font-weight: bold
		.cta_carousel_block_cards
			width: 320px
			flex-shrink: 0

		.cta_carousel_block_card
			background-color: #fff
			width: 260px
			height: 330px
			position: relative
			border-radius: 15px
			box-shadow: 0 3px 6px #00000033


swiper-container
	width: 240px
	height: 320px

swiper-slide
	display: flex
	align-items: center
	justify-content: center
	border-radius: 18px
	font-size: 22px
	font-weight: bold
	color: #fff

.swiper-slide-shadow-cards
	background-color: transparent !important

.swiper_button_cta_prev .swiper_button_cta_next
	background-color: #fff
	box-shadow: 0 3px 6px #00000033
